.App {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: white;
  overflow-y: hidden;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

a { text-decoration: none; }

a:hover {
  color: cyan;
}

/* selected link */
a:active {
  text-decoration: underline;
}

.App-header {
  z-index: 1;
  padding: 10px;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(8px + 0.5vw);
  color: #0f0f0f;
  width: 100%;
}

.App-category-title {
  margin-top: 0;
  margin-right: 20px;
  color: #0f0f0f;
  font-weight: 300;
}

.App-category-title-clicked {
  margin-top: 0;
  margin-right: 20px;
  color: #0f0f0f;
  font-weight: 300;
  text-decoration: underline;
}

.App-body {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(8px + 0.5vw);
  color: #0f0f0f;
}

.App-category-title:hover {
  color: cyan;
  font-weight: 500;
}

.Page-body {
  padding: 10px;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.App-footer {
  z-index: 1;
  bottom: 0;
  left: 0;
  padding: 10px;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(8px + 0.5vw);
  color: #0f0f0f;
  width: 100%;
}